
.searchListMain{width:8.5rem;height:7.5rem;margin-left:.14rem
}
.searchListMain .searchListBar{width:inherit;height:1rem
}
.searchListMain .searchListBar .searchListInput{width:6rem;padding-top:0.18rem;margin-left:.14rem
}
.searchListMain .searchListBar .searchListInput .searchListLogo{width:0.38rem;height:0.38rem;margin-top:0.02rem;background:url(../../../static/img/extended/searchlist/searchListLogo.png) no-repeat;background-size:100%;float:left
}
.searchListMain .searchListBar .searchListInput #searchListELInput{width:5rem;margin-left:.14rem
}
.searchListMain .searchListBar .searchListInput #searchListELInput input{background:#040E28 !important;border:2px solid #309BFF !important;float:left
}
.searchListMain .searchListBar .searchListTab{margin-top:0.1rem;margin-left:0.6rem;border:none
}
.searchListMain .searchListBar .searchListTab li{padding:0;margin-left:0.25rem;color:#B8BCC5;background:none
}
.searchListMain .searchListBar .searchListTab li:hover{color:#2D97FB;text-decoration:underline;background:none
}
.searchListMain .searchListBar .searchListTab .active{color:#2D97FB;background:none
}
.searchListMain .searchResultList{height:5.6rem;color:#FFF
}
.searchListMain .searchResultList .oneResult{width:inherit;height:1.3rem;margin-top:-1px;border-top:1px solid #0b1a40;border-bottom:1px solid #0b1a40;padding:0.15rem;position:relative
}
.searchListMain .searchResultList .oneResult .textHighlight{color:#ff9600
}
.searchListMain .searchResultList .oneResult .oneResultTitle{height:0.3rem;margin-left:0.3rem;padding-top:0.05rem;font-size:0.18rem;line-height:0.3rem
}
.searchListMain .searchResultList .oneResult .oneResultContent{height:0.6rem;margin:0.05rem 0.3rem;margin-right:0.55rem;font-size:0.14rem;text-indent:0.28rem;line-height:0.18rem
}
.searchListMain .searchResultList .oneResult .oneResultType{height:0.2rem;margin-left:0.3rem;text-indent:0.28rem
}
.searchListMain .searchResultList .oneResult .rightArrow{position:absolute;right:0.2rem;top:0.75rem;color:#467BCB
}
.searchListMain .searchResultList .oneResult:hover{background-color:#0b1a40;cursor:pointer
}



